body {
  font-family: 'Roboto', sans-serif;
  background-color: #f0f4f8;
  color: #333;
  margin: 0;
  padding: 0;
  line-height: 1.6;
}

.header {
  background-color: #4caf50;
  color: #fff;
  padding: 50px 20px;
  text-align: center;
  background: linear-gradient(to right, #4caf50, #2e7d32);
}

.header h1 {
  margin: 0;
  font-size: 2.5em;
}

.navbar {
  display: flex;
  justify-content: space-around;
  background-color: #333;
  padding: 10px 0;
}

.navbar a {
  color: #fff;
  text-decoration: none;
  padding: 10px 20px;
  font-weight: bold;
  transition: background-color 0.3s;
}

.navbar a:hover {
  background-color: #575757;
}

.main-content {
  padding: 40px 20px;
  max-width: 1200px;
  margin: 0 auto;
}

.hero {
  background-color: #8bc34a;
  padding: 80px 20px;
  color: #fff;
  text-align: center;
  background: linear-gradient(to right, #8bc34a, #689f38);
  margin-bottom: 40px;
}

.hero h2 {
  margin: 0;
  font-size: 2.5em;
  font-weight: 300;
}

.form-container {
  background-color: #fff;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  margin-bottom: 40px;
}

.form-group {
  margin-bottom: 15px;
}

.form-group label {
  display: block;
  margin-bottom: 5px;
  font-weight: bold;
}

.form-group input,
.form-group select {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

.form-group button {
  background-color: #4caf50;
  color: #fff;
  border: none;
  padding: 10px 20px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 1em;
  transition: background-color 0.3s;
}

.form-group button:hover {
  background-color: #388e3c;
}

.calendar {
  margin-top: 20px;
  background-color: #fff;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.calendar h3 {
  margin-bottom: 15px;
  font-weight: bold;
}

.footer {
  background-color: #333;
  color: #fff;
  text-align: center;
  padding: 20px;
  position: fixed;
  bottom: 0;
  width: 100%;
}
